import React from 'react';
import styled from 'styled-components';

interface ICosmoObject {
    navigation: number[];
    distance: number;
    time: number;
    center: number[];
    type: string;
    size: number;
    info: any;
    rot: number;
}
const Trace = styled.div`
    border: 2px dashed hsl(269, 73%, 73%);
    border-radius: 50%;
    position: absolute;
`;

const CosmoObject: React.FC<ICosmoObject> = ({distance, navigation, time, center, size, info, rot}) => {
    const [selected, selectPlanet] = React.useState(false);
    const [colorSeed] = React.useState(Math.floor(Math.random() * 150) + (distance * 2));
    React.useEffect(() => {
        if (!!selected) {
            const clickHandler = (event: any) => {
                if(event.target.className !== 'referencer') selectPlanet(false);
            }
            window.addEventListener('click', clickHandler);
            return () =>  window.removeEventListener('click', clickHandler);
        }   
    })

    const PeriodicVariation = (type: string) => {
        // return time%cicle > cicle/2 ? cicle - time%cicle : time%cicle;
        return type === 'x' ? Math.cos((time)/Math.pow(distance/2, 2)) : Math.sin(time/Math.pow(distance/2, 2));
    }

    const periodicY = PeriodicVariation(`y`);
    const periodicX = PeriodicVariation('x');
    return <>
     <Trace style={{width: `calc(${(distance*2) - 4}px)`, 
    height: `calc(${(distance*2) - 4}px)`, 
    left: `calc(${center[0]}vw - ${distance - 2}px)`, 
    top: `calc(${center[1]}vh - ${distance - 2}px)`}}/>
    

    <div onClick={() => selectPlanet(true)} style={{
        width: `${selected ? size * 2 : size * 1.5}px`, height: `${selected ? size * 2 : size * 1.5}px`, 
        zIndex: selected ? 10 : 5, cursor: 'pointer',
        // backgroundImage: `linear-gradient(
        //     ${periodicX > 0 ? (1 + periodicY) * 90 : 360 - ((1 + periodicY) * 90) }deg, 
        //     hsl(200, 73%, 73%) 0%,
        //     hsl(200, 73%, 63%) 15%, 
        //     hsl(200, 73%, 3%))`,
        backgroundImage: `radial-gradient(circle at ${50 - (periodicX * 50)}% ${50 - (periodicY * 50)}%, 
        hsl(${colorSeed}, 73%, 73%) 0%,
        hsl(${colorSeed}, 73%, 63%) 10%, 
        hsl(${colorSeed}, 73%, 3%))`,
        transform: `rotateY(-${rot}deg)`,
        border: `0px solid ${selected ? 'hsl(269, 73%, 93%)' : 'hsl(269, 73%, 53%)'}`, borderRadius: '50%',
        position: 'absolute', 
        top: `calc(${50}vh - ${size * (selected ? 1 : 0.75)}px - ${navigation[1]/2}vh + ${periodicY * distance}px)`, 
        left: `calc(${50}vw - ${size * (selected ? 1 : 0.75)}px + ${navigation[0]/2}vw + ${periodicX * distance}px)`
    }}>     
     {selected ? <div className={'referencer'} style={{backgroundColor: '#eee', padding: '5px', 
        justifyContent: 'center', textAlign: 'center',
        // marginTop: `calc(4px + ${size * 2}px)`,
        position: 'absolute', top: periodicY > 0 ? '-160px' : '', bottom: periodicY < 0 ? '-160px' : '',
        width: '150px', height: '140px',
        marginLeft: '-60px'}}>
            <div>
            <a href={info.link}>{info.name}</a>
            </div>
            {/* <a href={info.link}>
                    {info.description}
                    </a> */}
            <div>
            </div>
            <div style={{
                borderTop: '25px solid #eee',
                borderLeft: '25px solid transparent',
                borderRight: '25px solid transparent',
                position: 'absolute',
                left: '55px',
                bottom: '-25px'
            }} />
            </div> : ''}

    </div>
   
    </>
}

export default CosmoObject;